import React, { Component } from "react"
import PropTypes from "prop-types"
import { Row, Col, Collapse } from "reactstrap"
import { Link, withRouter } from "react-router-dom"
import classname from "classnames"

//i18n
import { withTranslation } from "react-i18next"

class Navbar extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }

  componentDidMount() {
    let matchingMenuItem = null
    const ul = document.getElementById("navigation")
    const items = ul.getElementsByTagName("a")
    for (let i = 0; i < items.length; ++i) {
      if (this.props.location.pathname === items[i].pathname) {
        matchingMenuItem = items[i]
        break
      }
    }
    if (matchingMenuItem) {
      this.activateParentDropdown(matchingMenuItem)
    }
  }

  activateParentDropdown = item => {
    item.classList.add("active")
    const parent = item.parentElement
    if (parent) {
      parent.classList.add("active") // li
      const parent2 = parent.parentElement
      parent2.classList.add("active") // li
      const parent3 = parent2.parentElement
      if (parent3) {
        parent3.classList.add("active") // li
        const parent4 = parent3.parentElement
        if (parent4) {
          parent4.classList.add("active") // li
          const parent5 = parent4.parentElement
          if (parent5) {
            parent5.classList.add("active") // li
            const parent6 = parent5.parentElement
            if (parent6) {
              parent6.classList.add("active") // li
            }
          }
        }
      }
    }
    return false
  }

  render() {
    return (
      <React.Fragment>
        <div className="topnav">
          <div className="container-fluid">
            <nav
              className="navbar navbar-light navbar-expand-lg topnav-menu"
              id="navigation"
            >
              <Collapse
                isOpen={this.props.menuOpen}
                className="navbar-collapse"
                id="topnav-menu-content"
              >
                <ul className="navbar-nav">
                  <li className="nav-item dropdown">
                    <Link
                      className="nav-link dropdown-toggle arrow-none"
                     
                      to="/dashboard"
                    >
                      
                      {this.props.t(" Dashboard")} {this.props.menuOpen}
                     
                    </Link>
                    
                  </li>

                  <li className="nav-item dropdown">
                    <Link
                      to="/#"
                      onClick={e => {
                        e.preventDefault()
                        this.setState({ uiState: !this.state.uiState })
                      }}
                      className="nav-link dropdown-toggle arrow-none"
                    >
                      
                      {this.props.t("Buyers")}{" "}
                      <div className="arrow-down" />
                    </Link>
                    <div
                      className={classname(
                        "dropdown-menu mega-dropdown-menu dropdown-menu-left dropdown-mega-menu-xl",
                        { show: this.state.uiState }
                      )}
                    >
                      <Row>
                        <Col lg={6}>
                          <div>
                            <Link to="#" className="dropdown-item">
                              {this.props.t("Market Updates")}
                            </Link>
                            <Link to="#" className="dropdown-item">
                              {this.props.t("Buyer Preferences")}
                            </Link>
                            <Link to="#" className="dropdown-item">
                              {this.props.t(" Buyer Showings")}
                            </Link>
                           
                          </div>
                        </Col>
                        <Col lg={6}>
                          <div>
                            <Link to="#" className="dropdown-item">
                              {this.props.t(" Buyer Closing")}
                            </Link>
                            <Link to="#" className="dropdown-item">
                              {this.props.t(" Buyer  Market")}
                            </Link>
                            <Link
                              to="#"
                              className="dropdown-item"
                            >
                              {this.props.t("Checklist")}
                            </Link>
                            
                          </div>
                        </Col>
                        
                      </Row>
                    </div>
                  </li>


                  <li className="nav-item dropdown">
                    <Link
                      to="/#"
                      onClick={e => {
                        e.preventDefault()
                        this.setState({ uiState: !this.state.uiState })
                      }}
                      className="nav-link dropdown-toggle arrow-none"
                    >
                      
                      {this.props.t("Sellers")}{" "}
                      <div className="arrow-down" />
                    </Link>
                    <div
                      className={classname(
                        "dropdown-menu mega-dropdown-menu dropdown-menu-left dropdown-mega-menu-xl",
                        { show: this.state.uiState }
                      )}
                    >
                      <Row>
                        <Col lg={6}>
                          <div>
                            <Link to="#" className="dropdown-item">
                              {this.props.t("Properties to Sell")}
                            </Link>
                            <Link to="#" className="dropdown-item">
                              {this.props.t("Seller Preference")}
                            </Link>
                            <Link to="#" className="dropdown-item">
                              {this.props.t("  Seller Showings")}
                            </Link>
                           
                          </div>
                        </Col>
                        <Col lg={6}>
                          <div>
                            <Link to="#" className="dropdown-item">
                              {this.props.t(" Seller Closing")}
                            </Link>
                            <Link to="#" className="dropdown-item">
                              {this.props.t(" Open House")}
                            </Link>
                            <Link
                              to="#"
                              className="dropdown-item"
                            >
                              {this.props.t("Checklist")}
                            </Link>
                            
                          </div>
                        </Col>
                        
                      </Row>
                    </div>
                  </li>
                  <li className="nav-item dropdown">
                    <Link
                      className="nav-link dropdown-toggle arrow-none"
                     
                      to="/dashboard"
                    >
                      
                      {this.props.t(" Projects")} {this.props.menuOpen}
                     
                    </Link>
                    
                  </li>

                  <li className="nav-item dropdown">
                    <Link
                      className="nav-link dropdown-toggle arrow-none"
                     
                      to="/messages"
                    >
                      
                      {this.props.t(" Messages")} {this.props.menuOpen}
                     
                    </Link>
                    
                  </li>


                  <li className="nav-item dropdown">
                    <Link
                      className="nav-link dropdown-toggle arrow-none"
                     
                      to="/documents"
                    >
                      
                      {this.props.t(" Documents")} {this.props.menuOpen}
                     
                    </Link>
                    
                  </li>

                  <li className="nav-item dropdown">
                    <Link
                      className="nav-link dropdown-toggle arrow-none"
                     
                      to="/appointment"
                    >
                      
                      {this.props.t(" Appointments")} {this.props.menuOpen}
                     
                    </Link>
                    
                  </li>
                </ul>
              </Collapse>
            </nav>
          </div>
        </div>
      </React.Fragment>
    )
  }
}

Navbar.propTypes = {
  location: PropTypes.object,
  menuOpen: PropTypes.any,
  t: PropTypes.any,
}

export default withRouter(withTranslation()(Navbar))
